//footer-area
.footer-area {
    .border-theme.footer__widget {
        @media #{$laptop} {
            padding-left: 40px;
        }
    }
}
.border-theme {
    position: relative;
    z-index: 1;
    &::before {
        content: "";
        position: absolute;
        width: 1px;
        height: 255px;
        background: $theme-color;
        left: 47px;
        top: 3px;
        z-index: 1;
        @media #{$laptop} {
            display: none;
        }
        @media #{$md} {
            display: none;
        }
        @media #{$xs} {
            display: none;
        }
    }
}
.footer__social {
    a {
        color: $white;
        font-size: 14px;
        background-color: rgb(22, 73, 100);
        width: 40px;
        height: 40px;
        font-size: 14px;
        line-height: 40px;
        display: inline-block;
        text-align: center;
        margin-right: 8px;
        &:hover {
            color: $white;
            background-color: $theme-color;
        }
    }
}

.footer__widget {
    p {
        color: #99b2bf;
    }
    .widget-title {
        font-size: 14px;
        font-weight: 700;
        line-height: 1.3;
        position: relative;
        z-index: 1;
        &::before {
            content: "";
            position: absolute;
            background: $theme-color;
            width: 20px;
            height: 1px;
            left: 0;
            top: 100%;
            z-index: 1;
        }
    }
}

.fot-list {
    padding: 0;
    margin: 0;
    a {
        color: #99b2bf;
        font-size: 16px;
        display: inline-block;
        margin-bottom: 16px;
        @include transition(0.3s);
        position: relative;
        z-index: 1;
        &::before {
            content: "";
            position: absolute;
            background: $theme-color;
            width: 5px;
            height: 1px;
            left: -20px;
            top: 50%;
            z-index: 1;
            opacity: 0;
        }
        &:hover {
            color: $white;
            transform: translateX(13px);
            -webkit-transform: translateX(13px);
            -moz-transform: translateX(13px);
            -ms-transform: translateX(13px);
            -o-transform: translateX(13px);
            &::before {
                opacity: 1;
            }
        }
    }
}
.fot-list-1 {
    li {
        &:nth-child(2) {
            a {
                &::before {
                    content: "\f658";
                }
            }
        }
        &:nth-child(3) {
            a {
                &::before {
                    content: "\f34e";
                }
            }
        }
        &:last-child {
            a {
                &::before {
                    content: "\f3c5";
                }
            }
        }
    }
    a {
        padding-left: 40px;
        &::before {
            content: "\f87b";
            font-family: "Font Awesome 5 pro";
            color: $white;
            font-size: 16px;
            background: transparent;
            opacity: 1;
            height: unset;
            width: unset;
            top: 0;
            left: 0;
        }

        span {
            color: $theme-color;
            font-weight: 700;
            border-bottom: 1px solid $theme-color;
        }
        &:hover {
            transform: translateX(0);
            -webkit-transform: translateX(0);
            -moz-transform: translateX(0);
            -ms-transform: translateX(0);
            -o-transform: translateX(0);
        }
    }
}

.subscribe-form {
    .input-box {
        position: relative;
        z-index: 1;
        &::before {
            content: "\f007";
            font-family: "Font Awesome 5 Pro";
            font-size: 16px;
            color: $white;
            left: 23px;
            top: 50%;
            transform: translateY(-50%);
            position: absolute;
            z-index: 1;
            -webkit-transform: translateY(-50%);
            -moz-transform: translateY(-50%);
            -ms-transform: translateY(-50%);
            -o-transform: translateY(-50%);
        }
        &.mail-input {
            &::before {
                content: "\f0e0";
            }
        }
    }
    input {
        border: 0;
        color: #4c7b94;
        font-size: 14px;
        width: 100%;
        height: 50px;
        padding-left: 43px;
        background: rgb(13, 71, 101);
        &::placeholder {
            color: #4c7b94;
        }
        &::-webkit-placeholder {
            color: #4c7b94;
        }
        &::-moz-placeholder {
            color: #4c7b94;
        }
        &::-o-placeholder {
            color: #4c7b94;
        }
    }
    .subscribe-btn {
        width: 100%;
        text-transform: capitalize;
        padding: 20px 0;
        border: 0;
        &:focus {
            box-shadow: 0;
        }
        &:hover {
            background: $heding3;
        }
    }
}

.copyright {
    & h6 {
        color: #a8bcc6;
        font-size: 14px;
        font-weight: 400;
        line-height: 1.4;
        a {
            color: $theme-color;
            font-weight: 700;
        }
    }
}

.footer-nav {
    padding: 0;
    margin: 0;
    padding-right: 25px;
    .nav-item {
        display: inline-block;
        .nav-link {
            color: $heding;
            font-size: 14px;
            font-weight: 700;
            line-height: 1;
            display: inline-block;
            position: relative;
            margin: 0 0 0 40px;
            padding: 30px 0;
            @media #{$xs} {
                margin-left: 10px;
            }
            &:hover {
                color: $theme-color;
            }
        }
    }
}

//footer-2
.footer-2 {
    .border-theme {
        &::before {
            background-color: rgb(237, 237, 237);
            height: 418px;
            top: -81px;
            left: 27px;
            @media #{$lg} {
                display: none;
            }
            @media #{$md} {
                display: none;
            }
            @media #{$xs} {
                display: none;
            }
        }
        &::after {
            content: "";
            position: absolute;
            width: 1px;
            background-color: rgb(237, 237, 237);
            height: 420px;
            top: -81px;
            right: -7px;
            z-index: 1;
            @media #{$lg} {
                display: none;
            }
            @media #{$md} {
                display: none;
            }
            @media #{$xs} {
                display: none;
            }
        }
        &.bor-right {
            &::before {
                display: none;
            }
            &::after {
                right: 54px;
            }
        }
    }
    .footer__widget {
        .widget-title {
            color: $heding;
        }
        p {
            color: $body-text-color;
        }
    }
    .fot-list {
        a {
            color: $body-text-color;
            &:hover {
                color: $heding;
            }
        }
    }
    .subscribe-form {
        .input-box::before {
            color: $heding;
        }
        input {
            color: #c7c7c7;
            border: 1px solid rgb(236, 236, 236);
            background-color: rgb(255, 255, 255);
            box-shadow: 0px 16px 32px 0px rgba(0, 0, 0, 0.04);
        }
        &::placeholder {
            color: #c7c7c7;
        }
        &::-webkit-placeholder {
            color: #c7c7c7;
        }
        &::-moz-placeholder {
            color: #c7c7c7;
        }
        &::-o-placeholder {
            color: #c7c7c7;
        }
    }
    .footer-nav {
        .nav-item {
            &:last-child {
                .nav-link {
                    margin-right: 0px;
                }
            }
            .nav-link {
                @media #{$md} {
                    margin-left: 25px;
                }
                @media #{$xs} {
                    margin-left: 0;
                    margin-right: 18px;
                }
            }
        }
    }
}

.section-footer {
    border-bottom: 1px solid rgb(237, 237, 237);
    .footer__social {
        a {
            color: $heding;
            border-radius: 50%;
            -webkit-border-radius: 50%;
            -moz-border-radius: 50%;
            -ms-border-radius: 50%;
            -o-border-radius: 50%;
            background: rgb(240, 245, 247);
            &:hover {
                color: $white;
                background: $theme-color;
            }
        }
    }
}

.copy-right-border {
    border-top: 1px solid rgb(237, 237, 237);
}
.copyright {
    p {
        a {
            color: $heding;
            font-weight: 700;
        }
    }
}

//footer-3
.footer-3 {
    .border-theme {
        &::before {
            background-color: rgba(255, 255, 255, 0.102);
            height: 418px;
        }
        &::after {
            background-color: rgba(255, 255, 255, 0.102);
            height: 418px;
        }
    }
    .footer__social {
        a {
            color: $white;
            background: rgba(255, 255, 255, 0.059);
            &:hover {
                background: $theme-color2;
            }
        }
    }
    .footer__widget {
        .widget-title {
            color: $white;
            font-size: 14px;
            &::before {
                background: $theme-color2;
            }
        }
        p {
            color: #a7a7a7;
        }
    }
    .fot-list {
        a {
            color: #a7a7a7;
            &::before {
                background: $theme-color2;
            }
            &:hover {
                color: $white;
            }
        }
    }
    .subscribe-form {
        input {
            color: #c7c7c7;
            background: $white;
        }
        .input-box::before {
            color: $heding;
        }
        &::placeholder {
            color: #c7c7c7;
        }
        &::-moz-placeholder {
            color: #c7c7c7;
        }
        &::-webkit-placeholder {
            color: #c7c7c7;
        }
        &::-o-placeholder {
            color: #c7c7c7;
        }
        .subscribe-btn {
            background: $theme-color2;
        }
    }

    .section-footer,
    .copy-right-border {
        border-color: rgba(255, 255, 255, 0.102);
    }
    .copy-right-border {
        padding-bottom: 6px;
    }
    .copyright {
        p {
            color: $body-text-color;
            a {
                color: $white;
            }
        }
    }
    .footer-nav {
        .nav-item {
            .nav-link {
                color: $white;
                @media #{$md} {
                    margin: 0 0 0 20px;
                }
                &:hover {
                    color: $theme-color2;
                }
            }
        }
    }
}
