//feature-area
.single-feature {
    position: relative;
    background: url(../img/feature//1.jpg) no-repeat top center;
    background-size: cover;
    padding: 77px 0 78px 70px;
    width: 100%;
    height: 100%;
    z-index: 2;
    @media #{$lg} {
        padding: 72px 0 78px 15px;
    }
    @media #{$xs} {
        padding: 72px 0 78px 15px;
    }
    &::before {
        position: absolute;
        content: "";
        background-color: rgb(8, 61, 89);
        opacity: 0.902;
        width: 100%;
        height: 100%;
        left: 0;
        top: 0;
        z-index: -1;
        @include transition(0.3s);
    }
    .f-icon {
        position: absolute;
        z-index: 1;
        right: 25px;
        top: 17px;
        @include transition(0.6s);
    }
    h6 {
        color: #a6b8c2;
        font-weight: 300;
        margin-bottom: 13px;
        & span {
            color: $white;
            font-weight: 400;
            margin-right: 18px;
        }
    }
    h2 {
        color: $white;
        font-size: 50px;
        line-height: 1.1;
        padding-left: 42px;
        @media #{$lg} {
            font-size: 30px;
        }
        @media #{$md} {
            font-size: 30px;
        }
        @media #{$xs} {
            font-size: 30px;
        }
    }
    &.feat-2 {
        h6 {
            color: #ffeedc;
        }
        &::before {
            background-color: rgb(255, 168, 75);
            opacity: 0.902;
        }
    }
    &.feat-3 {
        &::before {
            background-color: rgb(0, 101, 113);
            opacity: 0.902;
        }
    }
    &:hover {
        h6 {
            color: $white;
        }
        &::before {
            background-color: rgb(255, 168, 75);
        }
        .f-icon {
            top: 25px;
            right: 50px;
            transform: scale(1.1);
            -webkit-transform: scale(1.1);
            -moz-transform: scale(1.1);
            -ms-transform: scale(1.1);
            -o-transform: scale(1.1);
        }
    }
}

//core-feature-area
.core-feature-area {
    position: relative;
    z-index: 2;
    &::before {
        content: "";
        position: absolute;
        background: url(../img/feature/core-1.jpg) no-repeat right;
        width: 615px;
        max-width: 100%;
        height: 100%;
        right: 0;
        top: 0;
        z-index: -1;
        @media #{$lg} {
            width: 100%;
            max-width: 50%;
        }
        @media #{$md} {
            display: none;
        }
        @media #{$xs} {
            display: none;
        }
    }
}
.core-wrapper {
    .section-title {
        @media #{$lg} {
            padding-right: 0;
        }
        @media #{$xs} {
            padding-right: 0;
        }
    }
}
.text-list {
    li {
        color: $heding;
        font-size: 16px;
        font-weight: 500;
        padding-left: 60px;
        margin-bottom: 30px;
        position: relative;
        z-index: 1;
        &::before {
            content: "\f00c";
            position: absolute;
            font-family: "Font awesome 5 Pro";
            font-size: 16px;
            color: $theme-color;
            left: 0;
            top: -7px;
            z-index: 1;
            width: 40px;
            height: 40px;
            line-height: 40px;
            background: #fff0e1;
            border-radius: 50%;
            -webkit-border-radius: 50%;
            -moz-border-radius: 50%;
            -ms-border-radius: 50%;
            -o-border-radius: 50%;
            text-align: center;
        }
    }
}

.video-wrapper {
    background: url(../img/feature/core-2.jpg) no-repeat center;
    background-size: cover;
    width: 100%;
    min-height: 500px;
    position: relative;
    @media #{$md} {
        min-height: 400px;
    }
    @media #{$xs} {
        min-height: 300px;
    }
    & .popup-video {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        -webkit-transform: translate(-50%, -50%);
        -moz-transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%);
        -o-transform: translate(-50%, -50%);
    }
}

//core-wrapper2
.core-wrapper2 {
    @media #{$laptop} {
        padding-left: 0;
    }
    @media #{$lg} {
        padding-left: 0;
    }
    @media #{$md} {
        padding-left: 0;
    }
    @media #{$xs} {
        padding-left: 0;
    }
}

.features-bg {
    background: url(../img/feature/feature-bg1.jpg) no-repeat;
    background-size: cover;
    background-position: center;
}
.feature-list {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 20px;
    margin-top: 20px;
    @media #{$xs} {
        display: block;
    }
    li {
        &:first-child,
        &:nth-child(3) {
            margin-top: -20px;
        }
    }
}

.single-feature2 {
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: $white;
    padding: 25px 35px 27px 30px;
    position: relative;
    z-index: 1;
    @media #{$lg} {
        padding: 25px 10px 25px 10px;
    }
    @media #{$xs} {
        padding: 25px 15px 25px 15px;
        margin-bottom: 20px;
    }
    @media #{$sm} {
        padding: 25px 25px 25px 25px;
        margin-bottom: 20px;
    }
    &::before {
        content: " ";
        position: absolute;
        left: 0;
        top: 0;
        z-index: 1;
        width: 100%;
        height: 0px;
        line-height: 40px;
        background: transparent;
        @include transition(0.3s);
        z-index: -1;
    }
    h2 {
        color: #c4cdd2;
        font-size: 40px;
        margin-right: 20px;
    }
    h6 {
        line-height: 22px;
    }
    &:hover {
        &::before {
            background: $theme-color;
            height: 2px;
        }
        h2 {
            color: $theme-color;
        }
    }
}

.feature-img-wrapper {
    position: relative;
    z-index: 1;

    .feature-one {
        position: absolute;
        z-index: 2;
        &.two {
            border: 10px solid $white;
            right: 0;
            top: 180px;
            @media #{$md} {
                top: 0;
            }
            @media #{$xs} {
                display: none;
            }
        }
        &.three {
            right: 50px;
            top: 20px;
            animation: float-bob-x infinite 5s linear;
            -webkit-animation: float-bob-x infinite 5s linear;
        }
    }
}

//core-feature-area2
.feature-number {
    font-size: 120px;
    -webkit-text-fill-color: transparent;
    -webkit-text-stroke: 1px;
    -webkit-text-stroke-color: $theme-color;
    @media #{$md} {
        font-size: 80px;
    }
    @media #{$xs} {
        font-size: 50px;
        text-align: center;
    }
}
.features-title {
    font-size: 40px;
    line-height: 1.2;
    @media #{$lg} {
        font-size: 30px;
    }
    @media #{$md} {
        font-size: 30px;
    }
    @media #{$xs} {
        font-size: 20px;
    }
}
.tab-wrapper2 {
    .nav-tabs {
        border: 0;
        display: block;
        width: 100%;
        .nav-link {
            text-align: start;
            display: block;
            font-size: 20px;
            font-weight: 700;
            color: #b1b5b8;
            border: 0;
            border-bottom: 1px solid rgb(240, 240, 240);
            position: relative;
            width: 93%;
            padding: 24px 0 22px 0;
            @media #{$lg} {
                font-size: 16px;
                padding: 24px 0px 25px 0;
            }
            @media #{$xs} {
                font-size: 16px;
                padding: 24px 10px 25px 0;
            }
            img {
                margin-right: 18px;
                transform: translateY(-2px);
                -webkit-transform: translateY(-2px);
                -moz-transform: translateY(-2px);
                -ms-transform: translateY(-2px);
                -o-transform: translateY(-2px);
            }
            i {
                float: right;
            }
            &.active {
                color: $heding;
            }
        }
    }
}
.content-box {
    p {
        @media #{$md} {
            margin-bottom: 5px;
        }
    }
    .text-content {
        @media #{$lg} {
            padding-left: 0;
        }
        @media #{$md} {
            padding-left: 0;
        }
        @media #{$xs} {
            padding-left: 0;
        }
    }
    .text-list {
        margin: 0;
        li {
            font-size: 16px;
            font-weight: 500;
            padding-left: 30px;
            margin-bottom: 15px;
            @media #{$lg} {
                margin-bottom: 8px;
            }
            @media #{$md} {
                font-size: 15px;
            }
            &::before {
                background: transparent;
                font-weight: 400;
                left: 0;
                width: unset;
                height: unset;
            }
        }
    }
}

.video-bg-1 {
    background: url(../img/video/video-bg1.jpg) no-repeat;
    background-position: center;
    background-size: cover;
    width: 100%;
    min-height: 700px;
    display: flex;
    align-items: center;
    @media #{$lg} {
        min-height: 550px;
    }
    @media #{$md} {
        min-height: 450px;
    }
    @media #{$xs} {
        min-height: 400px;
    }
}
