.semi-title {
    color: $heding2;
    font-size: 14px;
    letter-spacing: 0px;
}

.sect-title {
    font-size: 60px;
    line-height: 1.17;
    span {
        font-weight: 100;
    }
    @media #{$lg} {
        font-size: 45px;
    }
    @media #{$md} {
        font-size: 45px;
    }
    @media #{$xs} {
        font-size: 30px;
    }
    .bottom-shape::before {
        left: 0;
        top: 63%;
    }
}

.paragraph-title {
    color: $body-text-color;
    font-size: 18px;
    line-height: 28px;
    strong {
        color: $heding;
        font-weight: 700;
    }
}
.semi-grey {
    color: #92aab6;
}

.semi-title2 {
    color: $theme-color2;
    font-size: 14px;
    letter-spacing: 0px;
    display: inline-block;
    position: relative;
    z-index: 1;
    .line-one {
        margin-right: 10px;
    }
    .line-two {
        margin-left: 10px;
    }
}

.sect-title2 {
    font-size: 60px;
    line-height: 1.1;
    color: $heding3;
    span {
        font-weight: 100;
    }
    @media #{$lg} {
        font-size: 45px;
    }
    @media #{$md} {
        font-size: 45px;
    }
    @media #{$xs} {
        font-size: 30px;
    }
    .bottom-shape::before {
        top: 70%;
    }
}

.section-title2 {
    .semi-title2 {
        margin-bottom: 13px;
        line-height: 1;
    }
    .sect-title2 {
        margin-bottom: 48px;
    }
}
