//about-content-wrapper
.about-content-wrapper {
    @media #{$lg} {
        padding-left: 0;
    }
    @media #{$md} {
        padding-left: 0;
    }
    @media #{$xs} {
        padding-left: 0;
    }
    .paragraph-title {
        margin-bottom: 46px;
    }
}
.tab-wrapper {
    padding-left: 60px;
    padding-right: 35px;
    @media #{$lg} {
        padding-left: 0;
        padding-right: 0;
    }
    @media #{$md} {
        padding-left: 0;
        padding-right: 0;
    }
    @media #{$xs} {
        padding-left: 0;
        padding-right: 0;
    }
    .nav-tabs {
        border: 0;

        .nav-link {
            color: $theme-color;
            font-weight: 700;
            line-height: 1;
            border: 1px solid $hr-border-color;
            border-radius: 0;
            -webkit-border-radius: 0;
            -moz-border-radius: 0;
            -ms-border-radius: 0;
            -o-border-radius: 0;
            padding: 19px 29px;
            &:first-child {
                margin-right: 20px;
            }
            @media #{$xs} {
                width: 100%;
                margin-bottom: 15px;
                &:first-child {
                    margin-right: 0;
                }
            }
            @media #{$sm} {
                width: inherit;
                margin-bottom: inherit;
                &:first-child {
                    margin-right: inherit;
                }
            }
            & span {
                color: $heding;
                font-size: 16px;
                margin-right: 50px;
                margin-left: 2px;
                @media #{$lg} {
                    margin-right: 15px;
                }
            }
            &.active {
                border-color: $heding;
                color: $theme-color;
                background: $heding;
                & span {
                    color: $white;
                }
            }
        }
    }
}

.shape-square {
    position: absolute;
    right: 80px;
    top: 40px;
    z-index: 1;
    animation: rotation infinite 5s linear;
}
//about-area-2
.about-area-2 {
    position: relative;
    z-index: 3;
}

.about-content-wrapper2 {
    .paragraph-title {
        padding-right: 70px;
        @media #{$md} {
            padding-right: 0;
        }
        @media #{$xs} {
            padding-right: 0;
        }
    }
}

.about-text {
    overflow: hidden;
    margin-bottom: 20px;
    padding-right: 80px;
    @media #{$lg} {
        padding-right: 0;
    }
    @media #{$xs} {
        padding-right: 0;
    }
    .img-box {
        float: left;
        width: 100px;
        margin-right: 30px;
        margin-bottom: 30px;
        margin-top: 7px;
        @media #{$xs} {
            float: none;
        }
        @media #{$sm} {
            float: left;
        }
    }

    p {
        overflow: hidden;
    }
}

.read-btn2 {
    color: #bfbfbf;
    display: block;
    transform: unset;
    background: rgb(241, 241, 241);
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 15px 30px;
    @include transition(0.3s);
    margin-left: 0;
}
.single-about {
    background-color: $white;
    box-shadow: 0px 16px 32px 0px rgba(0, 0, 0, 0.04);
    img {
        width: 100%;
        margin-bottom: 27px;
    }
    h5 {
        padding-left: 30px;
        line-height: 1;
        margin-bottom: 4px;
    }
    p {
        font-size: 14px;
        padding-left: 30px;
        padding-bottom: 24px;
    }
    &:hover {
        .read-btn2 {
            color: $white;
            background-color: $heding;
        }
    }
}

.single-about-wrapper {
    margin-top: -20px;
    margin-right: 0;
    margin-left: 0;
    @media #{$md} {
        margin-top: 0;
    }
    .col-xl-6:first-child {
        margin-top: -60px;
        .single-about {
            margin-right: 5px;
        }
        @media #{$md} {
            margin-top: 0;
        }
        @media #{$xs} {
            margin-top: 30px;
        }
    }
    .col-xl-6:nth-child(2) {
        margin-top: -20px;
        .single-about {
            margin-left: 5px;
        }
        @media #{$lg} {
            .single-about {
                margin-left: 0;
            }
        }
        @media #{$md} {
            margin-top: 0;
            .single-about {
                margin-left: 0;
            }
        }
        @media #{$xs} {
            margin-top: 0;
            .single-about {
                margin-left: 0;
            }
        }
    }
    .col-xl-6:nth-child(3) {
        margin-top: -40px;
        .single-about {
            margin-right: 5px;
        }
        @media #{$md} {
            .single-about {
                margin-right: 0;
            }
        }
        @media #{$md} {
            margin-top: 0;
            .single-about {
                margin-right: 0;
            }
        }
        @media #{$xs} {
            margin-top: 0;
            .single-about {
                margin-right: 0;
            }
        }
    }
    .col-xl-6:nth-child(4) {
        .single-about {
            margin-left: 5px;
            @media #{$lg} {
                margin-left: 0;
            }
            @media #{$md} {
                margin-left: 0;
            }
            @media #{$xs} {
                margin-left: 0;
            }
        }
    }
}

//about-img-wrapper
.about-img-wrapper3 {
    position: relative;
    z-index: 1;
    .about-one {
        position: absolute;
        @media #{$md} {
            position: relative;
        }
        @media #{$xs} {
            position: relative;
        }
        &.img-1 {
            top: -322px;
            bottom: auto;
            left: 0;
        }
        &.img-2 {
            top: -75px;
            right: 20px;
            border: 10px solid white;
            @media #{$md} {
                top: 0;
                right: 0;
                text-align: center;
                width: 100%;
            }
            @media #{$xs} {
                top: 0;
                right: 0;
                width: 100%;
            }
        }
        &.img-3 {
            position: absolute;
            left: -37px;
            top: -214px;
            animation: float-bob-y infinite 3s linear;
            -webkit-animation: float-bob-y infinite 3s linear;
            @media #{$md} {
                left: 0;
                top: 0;
            }
            @media #{$xs} {
                left: 0;
                top: 0;
            }
        }
    }
}

//about-content-wrapper2
.about-content-wrapper2 {
    @media #{$lg} {
        padding-left: 0;
    }
    @media #{$md} {
        padding-left: 0;
    }
    @media #{$xs} {
        padding-left: 0;
    }
    .section-title2 {
        .sect-title2 {
            margin-bottom: 20px;
        }
    }
    .paragraph-title {
        margin-bottom: 39px;
        @media #{$lg} {
            padding-right: 0;
        }
        @media #{$md} {
            padding-right: 0;
        }
        @media #{$xs} {
            padding-right: 0;
        }
    }
    .about-text {
        padding-right: 50px;
        margin-bottom: 37px;
        @media #{$lg} {
            padding-right: 0;
        }
        @media #{$md} {
            padding-right: 0;
        }
        @media #{$xs} {
            padding-right: 0;
        }
        .text-box {
            background-color: $white;
            box-shadow: 0px 16px 32px 0px rgba(0, 0, 0, 0.06);
            width: 100px;
            height: 110px;
            text-align: center;
            float: left;
            overflow: hidden;
            margin-right: 30px;
            h2 {
                color: $theme-color2;
                font-size: 40px;
                font-weight: 700;
                padding-top: 20px;
            }
            span {
                color: $body-text-color;
                font-size: 12px;
                display: block;
                line-height: 16px;
            }
            @media #{$xs} {
                float: none;
            }
            @media #{$sm} {
                float: inherit;
            }
        }
    }
}
.author-media {
    display: grid;
    grid-template-columns: repeat(2, 2fr);
    grid-gap: 21px;
    align-items: center;
    border-top: 1px solid rgb(240, 240, 240);
    padding-top: 40px;
    @media #{$lg} {
        grid-gap: 15px;
    }
    @media #{$xs} {
        display: block;
        grid-gap: 15px;
    }
    li {
        position: relative;
        z-index: 1;
        &:first-child {
            &::before {
                content: "";
                position: absolute;
                background-color: rgb(240, 240, 240);
                width: 1px;
                height: 50px;
                right: 20px;
                top: 9px;
                z-index: 1;
            }
        }
    }
    .footer__social {
        @media #{$xs} {
            margin-top: 25px;
        }
        a {
            color: $heding3;
            border-radius: 50%;
            background: #f2f2f2;
            &:hover {
                color: $white;
                background: $theme-color2;
            }
        }
    }
}

//about-us-2
.about-us-2 {
    .about-content-wrapper3 {
        @media #{$laptop} {
            padding-left: 0;
        }
        @media #{$lg} {
            padding-left: 0;
        }
        @media #{$md} {
            padding-left: 0;
        }
        @media #{$xs} {
            padding-left: 0;
        }
        .paragraph-title {
            padding-right: 0;
            margin-top: 30px;
            margin-bottom: 29px;
        }
        .about-btn {
            margin-top: 41px;
        }
    }
}

//our-journey-area
.our-journey-area {
    background-image: -moz-linear-gradient(90deg, rgb(255, 255, 255) 0%, rgb(246, 246, 246) 100%);
    background-image: -webkit-linear-gradient(90deg, rgb(255, 255, 255) 0%, rgb(246, 246, 246) 100%);
    background-image: -ms-linear-gradient(90deg, rgb(255, 255, 255) 0%, rgb(246, 246, 246) 100%);
}

.about-btn {
    li {
        .theme_btn2 {
            @media #{$xs} {
                margin-left: 0;
            }
        }
        &:first-child {
            .theme_btn2 {
                color: $white;
                background: $heding;
                border: 1px solid transparent;
                &:hover {
                    color: $heding;
                    background: transparent;
                    border-color: rgb(237, 237, 237);
                }
            }
        }
        &:last-child {
            .theme_btn2 {
                color: $heding;
                border: 1px solid rgb(237, 237, 237);
                background: transparent;
                &:hover {
                    color: $white;
                    background: $heding;
                }
            }
        }
    }
}

//time-line
.timeline-box {
    padding-left: 0;
    padding-right: 0;
    align-items: center;
    @media #{$lg} {
        margin-bottom: 0;
    }
    .left-content {
        h2 {
            color: $theme-color;
            font-size: 80px;
            margin-bottom: 20px;
            @media #{$lg} {
                font-size: 60px;
            }
            @media #{$md} {
                font-size: 60px;
            }
            &.heding-grey {
                color: #ededed;
            }
        }
        h4 {
            font-size: 24px;
            margin-bottom: 10px;
            @media #{$lg} {
                font-size: 20px;
            }
            @media #{$md} {
                font-size: 20px;
            }
        }
    }
}

.journey-list {
    display: grid;
    grid-template-columns: repeat(2, 2fr);
    align-items: center;
    justify-content: space-between;
    grid-gap: 30px;
    position: relative;
    z-index: 1;
    padding-left: 0;
    margin: 0;
    @media #{$lg} {
        padding-top: 0;
        grid-gap: 15px;
    }
    @media #{$md} {
        padding-top: 0;
    }
    @media #{$xs} {
        padding-top: 0;
        display: block;
    }
    &::before {
        content: "";
        position: absolute;
        background: url(../img/journey/time-line1.png) no-repeat;
        width: 100px;
        height: 100%;
        top: 0;
        left: 0;
        right: 0;
        margin: 0 auto;
        text-align: center;
        z-index: -1;
        @media #{$laptop} {
            display: none;
        }
        @media #{$lg} {
            display: none;
        }
        @media #{$md} {
            display: none;
        }
        @media #{$xs} {
            display: none;
        }
    }
    li {
        margin-top: 30px;
        position: relative;
        z-index: 1;
        @media #{$xs} {
            margin-top: 0;
        }
        img {
            @media #{$xs} {
                width: 100%;
            }
        }
        &:first-child,
        &:nth-child(3),
        &:nth-child(5),
        &:nth-child(7) {
            padding-right: 60px;
            @media #{$lg} {
                padding-right: 0;
            }
            @media #{$md} {
                padding-right: 0;
            }
            @media #{$xs} {
                padding-right: 0;
            }
        }

        &:nth-child(2),
        &:nth-child(4),
        &:nth-child(6),
        &:nth-child(8) {
            transform: translateY(100px);
            -webkit-transform: translateY(100px);
            -moz-transform: translateY(100px);
            -ms-transform: translateY(100px);
            -o-transform: translateY(100px);
            padding-left: 45px;
            @media #{$lg} {
                transform: translateY(0);
                -webkit-transform: translateY(0);
                -moz-transform: translateY(0);
                -ms-transform: translateY(0);
                -o-transform: translateY(0);
                padding-left: 0;
            }
            @media #{$md} {
                transform: translateY(0);
                -webkit-transform: translateY(0);
                -moz-transform: translateY(0);
                -ms-transform: translateY(0);
                -o-transform: translateY(0);
                padding-left: 0;
            }
            @media #{$xs} {
                transform: translateY(0);
                -webkit-transform: translateY(0);
                -moz-transform: translateY(0);
                -ms-transform: translateY(0);
                -o-transform: translateY(0);
                padding-left: 0;
            }
        }
