.rating {
    & a {
        color: $theme-color2;
        font-size: 14px;
        font-weight: 300;
    }
}
.testimonial-wrapper {
    background: $white;
    box-shadow: 0px 16px 32px 0px rgba(0, 0, 0, 0.04);
    padding: 45px 55px 50px 50px;
    @media #{$laptop} {
        padding: 45px 30px 50px 30px;
    }
    @media #{$lg} {
        padding: 45px 25px 50px 25px;
    }
    @media #{$md} {
        padding: 45px 25px 50px 25px;
    }
    @media #{$xs} {
        padding: 35px 15px 35px 15px;
    }
    p {
        font-size: 20px;
        padding-bottom: 25px;
        border-bottom: 1px solid rgb(239, 239, 239);
    }
    .author-box {
        margin-top: 28px;
    }
    .author-content {
        p {
            border: 0;
            font-size: 14px;
        }
    }
}
