//services-area
.services-bg {
    position: relative;
    z-index: 2;
    &::before {
        content: "";
        position: absolute;
        background: $grey2;
        width: 100%;
        height: 528px;
        left: 0;
        top: 0;
        z-index: -1;
        @media #{$lg} {
            height: 485px;
        }
        @media #{$md} {
            height: 100%;
        }
        @media #{$xs} {
            display: none;
        }
    }
}

.service-text {
    background-color: $white;
    box-shadow: 0px 16px 32px 0px rgba(0, 0, 0, 0.04);
    padding: 19px 55px 23px 18px;
    margin: 0 0 23px 0;
    @include transition(0.3s);
    @media #{$lg} {
        padding-left: 5px;
        padding-right: 5px;
    }
    @media #{$md} {
        padding-left: 10px;
        padding-right: 10px;
        margin-bottom: 20px;
    }
    @media #{$xs} {
        padding-left: 5px;
        padding-right: 5px;
    }
    img {
        margin-right: 22px;
        @media #{$lg} {
            margin-right: 2px;
        }
    }
}

.single-services {
    @include transition(0.3s);
    position: relative;
    z-index: 3;
    .services__thumb {
        position: relative;
        z-index: 1;
        img {
            width: 100%;
        }
    }
    .services__content {
        background: transparent;
        position: absolute;
        z-index: 3;
        bottom: -90px;
        left: 0;
        right: 0;
        margin-left: 40px;
        margin-right: 40px;
        @media #{$lg} {
            margin-left: 15px;
            margin-right: 15px;
        }
        @media #{$md} {
            margin-left: 15px;
            margin-right: 15px;
        }
        @media #{$xs} {
            margin-left: 15px;
            margin-right: 15px;
        }
    }
    p {
        color: #97b4c3;
        text-align: left;
        display: none;
        border-top: 1px solid rgb(23, 75, 103);
        padding: 23px 30px 30px 30px;
        @include transition(0.3s);
        @media #{$lg} {
            padding: 10px 5px;
            line-height: 18px;
        }
        @media #{$xs} {
            padding: 10px 5px;
            line-height: 18px;
        }
    }
    &:hover {
        .services__content {
            background: $heding;
            bottom: -114px;
        }
        .service-text {
            background: transparent;
            color: $white;
            box-shadow: 0;
            margin-bottom: 0;
        }
        p {
            display: block;
        }
        .service-btn {
            color: $white;
            background: $theme-color;
            padding: 16px 0;
        }
    }
}

//brand-item
.brand-area {
    .brand-item {
        text-align: center;
        .brand-front {
            display: inline-block;
            @include transition(0.3s);
        }
        .brand-back {
            display: none;
            @include transition(0.3s);
        }
        &:hover {
            .brand-front {
                display: none;
            }
            .brand-back {
                display: inline-block;
            }
        }
    }
    .owl-carousel .owl-item img {
        display: inline-block;
        max-width: 100%;
        width: unset;
    }
}
.border-line {
    border-bottom: 1px solid rgb(224, 224, 224);
}
.brand-title {
    text-align: center;
    & h5 {
        font-size: 14px;
        padding-left: 25px;
        padding-right: 25px;
    }
}

.border-bg {
    position: relative;
    z-index: 1;
    display: block;
    &::before {
        content: "";
        position: absolute;
        background: #e0e0e0;
        left: 0;
        top: 8px;
        z-index: 1;
        width: 45%;
        height: 1px;
        @media #{$lg} {
            width: 30%;
        }
        @media #{$md} {
            width: 30%;
        }
        @media #{$xs} {
            display: none;
        }
    }
    &::after {
        content: "";
        position: absolute;
        background: #e0e0e0;
        right: 0;
        top: 8px;
        z-index: 1;
        width: 45%;
        height: 1px;
        @media #{$lg} {
            width: 30%;
        }
        @media #{$md} {
            width: 30%;
        }
        @media #{$xs} {
            display: none;
        }
    }
}

//services-bg2
.services-bg2 {
    background: #e7eef2;
    padding: 120px 60px 90px 60px;
    position: relative;
    z-index: 2;
    @media #{$lg} {
        padding: 120px 15px 90px 15px;
    }
    @media #{$md} {
        padding: 95px 15px 30px 15px;
    }
    @media #{$xs} {
        padding: 80px 15px 30px 15px;
    }
    .section-titl {
        position: relative;
        z-index: 2;
    }
}

.style-text {
    font-size: 200px;
    line-height: 1;
    background: #ffffff;
    background: -webkit-linear-gradient(to bottom, #ffffff 0%, #e7eef2 100%);
    background: -moz-linear-gradient(to bottom, #ffffff 0%, #e7eef2 100%);
    background: linear-gradient(to bottom, #ffffff 0%, #e7eef2 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    text-align: center;
    position: absolute;
    top: 32px;
    left: 0;
    right: 0;
    z-index: -1;
    @media #{$md} {
        font-size: 100px;
    }
    @media #{$xs} {
        font-size: 70px;
    }
}

.single-services2 {
    text-align: center;
    background-color: $white;
    margin-bottom: 30px;
    .services-content {
        padding: 50px 50px 35px 50px;
        @media #{$laptop} {
            padding: 50px 25px 35px 25px;
        }
        @media #{$lg} {
            padding: 50px 15px 35px 15px;
        }
        @media #{$md} {
            padding: 50px 15px 35px 15px;
        }
        @media #{$xs} {
            padding: 50px 15px 35px 15px;
        }
        h4 {
            font-size: 24px;
            margin-bottom: 7px;
            line-height: 1.4;
            @media #{$xs} {
                font-size: 20px;
            }
        }
    }
    .icon {
        @include transition(0.3s);
    }
    .read-btn2 {
        color: $heding;
        background: transparent;
        border-top: 1px solid rgb(231, 238, 242);
        padding: 20px 40px;
        font-weight: 700;
        i {
            color: #b3b3b3;
        }
        &:hover {
            color: $white;
            background: $heding;
        }
    }
    &:hover {
        .icon {
            transform: translateY(-7px);
            -webkit-transform: translateY(-7px);
            -moz-transform: translateY(-7px);
            -ms-transform: translateY(-7px);
            -o-transform: translateY(-7px);
        }
    }
}

//brand-bg
.brand-bg {
    background: url(../img/brand/brand-bg1.jpg) no-repeat center;
    background-size: cover;
    width: 100%;
    height: 100%;
    position: relative;
    z-index: 2;
    &::before {
        content: "";
        position: absolute;
        background: $heding;
        opacity: 0.9;
        width: 100%;
        height: 100%;
        left: 0;
        top: 0;
        z-index: -1;
    }

    .brand-title {
        color: $white;
        text-align: center;
        position: relative;
        margin: 0 auto;
        z-index: 4;
        @media #{$md} {
            &::before {
                width: 100px;
            }
            &::after {
                width: 100px;
            }
        }
        @media #{$xs} {
            &::before {
                display: none;
            }
            &::after {
                display: none;
            }
        }
        &::before {
            content: "";
            position: absolute;
            background: rgba(255, 255, 255, 0.2);
            opacity: 0.9;
            width: 548px;
            height: 1px;
            left: -23px;
            top: 50%;
            z-index: 1;
            @media #{$lg} {
                width: 30%;
            }
            @media #{$md} {
                width: 20%;
            }
            @media #{$xs} {
                display: none;
            }
        }
        &::after {
            content: "";
            position: absolute;
            background: rgba(255, 255, 255, 0.2);
            opacity: 0.9;
            width: 548px;
            height: 1px;
            right: 23px;
            top: 50%;
            z-index: 1;
            @media #{$lg} {
                width: 30%;
            }
            @media #{$md} {
                width: 20%;
            }
            @media #{$xs} {
                display: none;
            }
        }
    }
}

//brand-bg3
.brand-bg3 {
    background: #f7f7f7;
}

.style-text2 {
    font-size: 140px;
    -webkit-text-fill-color: transparent;
    -webkit-text-stroke-width: 1px;
    -webkit-text-stroke-color: $body-text-color;
    opacity: 0.1;
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    text-align: center;
    @media #{$xs} {
        font-size: 60px;
    }
}
.section-title2 {
    position: relative;
    z-index: 1;
    .style-text2 {
        top: -44px;
    }
}
.single-services3 {
    border: 1px solid rgb(231, 231, 231);
    background-color: $white;
    box-shadow: 0px 16px 32px 0px rgba(0, 0, 0, 0.04);
    position: relative;
    z-index: 1;
    padding: 50px 50px 25px 50px;
    @media #{$laptop} {
        padding: 50px 40px 25px 50px;
    }
    @media #{$lg} {
        padding: 50px 15px 25px 15px;
    }
    @media #{$xs} {
        padding: 50px 15px 25px 15px;
    }

    .shadow-text {
        font-size: 250px;
        color: rgb(247, 247, 247);
        line-height: 0.28;
        right: -3px;
        position: absolute;
        top: 77px;
        z-index: -1;
        @media #{$lg} {
            font-size: 150px;
        }
        @media #{$md} {
            font-size: 100px;
        }
        @media #{$xs} {
            font-size: 100px;
        }
    }
    .icon {
        margin-bottom: 37px;
    }
    h4 {
        color: $heding3;
        font-size: 24px;
        margin-bottom: 18px;
    }
    p {
        line-height: 26px;
        margin-bottom: 6px;
    }
    .read-btn2 {
        display: inline-block;
        font-weight: 700;
        font-size: 16px;
        background: transparent;
        padding-left: 0;
        i {
            margin-left: 15px;
        }
        &:hover {
            color: $theme-color2;
        }
    }
    &:hover {
        .shadow-text {
            color: rgb(243, 242, 242);
        }
    }
}

//service-feature
.service-feature-bg {
    background: url(../img/services/service-bg1.jpg) no-repeat;
    min-height: 926px;
    background-position: center;
    display: flex;
    align-items: center;
    background-size: cover;
    position: relative;
    margin-right: 100px;
    margin-left: 100px;
    margin-bottom: -220px;
    z-index: 2;
    @media #{$lg} {
        min-height: 650px;
        margin-bottom: 0;
        margin-right: 0;
        margin-left: 0;
    }
    @media #{$md} {
        min-height: 620px;
        margin-bottom: 0;
        margin-right: 0;
        margin-left: 0;
    }
    @media #{$xs} {
        min-height: 480px;
        margin-bottom: 0;
        margin-right: 0;
        margin-left: 0;
    }
}

.service-feature-wrapper {
    padding: 95px 100px 75px 100px;
    background: $white;
    border-left: 4px solid $theme-color;
    @media #{$laptop} {
        padding: 50px 50px 50px 50px;
    }
    @media #{$lg} {
        padding: 35px 50px 25px 50px;
    }
    @media #{$md} {
        padding: 15px 25px 15px 25px;
    }
    @media #{$xs} {
        padding: 15px 15px 15px 15px;
    }
    .sect-title {
        margin-bottom: 28px;
        @media #{$xs} {
            margin-bottom: 10px;
        }
    }
    .feature-btn-list {
        padding: 0;
        margin: 0;
        li {
            padding: 21px 0 25px 0;
            display: block;
            @media #{$md} {
                padding: 15px 0;
            }
            @media #{$xs} {
                padding: 8px 0;
            }
            &:nth-child(2) {
                border-top: 1px solid rgb(232, 232, 232);
                border-bottom: 1px solid rgb(232, 232, 232);
                @media #{$xs} {
                    border: 0;
                }
            }
        }
    }
    .service-btn {
        color: $heding;
        font-size: 20px;
        font-weight: 700;
        position: relative;
        @media #{$md} {
            font-size: 18px;
        }
        @media #{$xs} {
            font-size: 16px;
        }
        img {
            @media #{$xs} {
                display: none;
            }
            @media #{$sm} {
                display: inline-block;
            }
        }
        i {
            color: #c7c7c7;
            position: absolute;
            right: 0;
            top: 50%;
        }
        span {
            margin-left: 35px;
            max-width: 200px;
            width: 100%;
            display: inline-block;
            transform: translateY(15px);
            -webkit-transform: translateY(15px);
            -moz-transform: translateY(15px);
            -ms-transform: translateY(15px);
            -o-transform: translateY(15px);
            @media #{$xs} {
                margin-left: 0;
            }
        }
    }
}

//services-left
.s-sub-title {
    font-size: 30px;
    @media #{$xs} {
        font-size: 20px;
    }
}
.details-box {
    border: 1px solid rgb(242, 242, 242);
    background: $white;
    box-shadow: 0px 16px 32px 0px rgba(0, 0, 0, 0.04);
    padding: 50px 40px 40px 40px;
    position: relative;
    z-index: 2;
    @include transition(0.3s);
    overflow: hidden;
    @media #{$laptop} {
        padding: 50px 20px 40px 20px;
    }
    @media #{$lg} {
        padding: 50px 15px 40px 15px;
    }
    @media #{$md} {
        padding: 50px 15px 40px 15px;
    }
    @media #{$xs} {
        padding: 50px 15px 40px 15px;
    }
    .shadow-text {
        font-size: 200px;
        -webkit-text-fill-color: transparent;
        -webkit-text-stroke-width: 1px;
        -webkit-text-stroke-color: #777777;
        opacity: 0;
        line-height: 0.28;
        right: -14px;
        position: absolute;
        top: 38px;
        z-index: -1;
    }
    img {
        position: relative;
        z-index: 3;
    }
    h5 {
        font-size: 20px;
        @media #{$lg} {
            font-size: 16px;
        }
        @media #{$md} {
            font-size: 16px;
        }
    }
    p {
        font-size: 14px;
    }
    &:hover {
        background: $heding;
        .shadow-text {
            opacity: 0.3;
        }
        h5 {
            color: $white;
        }
        p {
            color: $white;
        }
    }
}

.services-left {
    p {
        a {
            color: $heding;
            font-weight: 700;
            border-bottom: 1px solid #083d59;
        }
    }
}

.blockquote {
    padding: 50px 75px 50px 75px;
    margin-bottom: 53px;
    position: relative;
    z-index: 1;
    @media #{$lg} {
        padding: 50px 20px 50px 20px;
    }
    @media #{$md} {
        padding: 50px 20px 50px 20px;
    }
    @media #{$xs} {
        padding: 40px 15px 30px 15px;
    }
    @media #{$sm} {
        padding: 50px 20px 45px 20px;
    }
    .q-icon {
        margin-bottom: 33px;
    }
    p.h4 {
        font-size: 24px;
        font-weight: 400;
        line-height: 1.4;
        margin-bottom: 35px;
        @media #{$xs} {
            font-size: 20px;
        }
    }
    &::before {
        position: absolute;
        content: "";
        background: url(../img/services/quote-lg.png) no-repeat;
        right: 62px;
        text-align: center;
        z-index: 1;
        width: 256px;
        height: 190px;
        z-index: -1;
    }
    .author-img {
        margin-right: 17px;
        img {
            border-radius: 50%;
            -webkit-border-radius: 50%;
            -moz-border-radius: 50%;
            -ms-border-radius: 50%;
            -o-border-radius: 50%;
        }
    }
    .author-content {
        margin-top: 4px;
        p {
            text-align: left;
        }
    }
}
.ham-icons {
    @media #{$xs} {
        margin: 15px 0 35px 0;
    }
}
.next-box {
    @media #{$xs} {
        .author-img {
            margin-left: 0;
            margin-right: 20px;
        }
    }
}
