//header
.heding-bg-1 {
	position: relative;
	background: $heding;
}
.main-header-area {
	position: relative;
	width: 100%;
	top: 16px;
	left: 0;
	z-index: 999;
}
.logo-area {
	& img {
		max-width: 100%;
	}
	.head-logo-1 {
		display: inline-block;
		position: relative;
		transform: translateY(5px);
		-webkit-transform: translateY(5px);
		-moz-transform: translateY(5px);
		-ms-transform: translateY(5px);
		-o-transform: translateY(5px);
	}
}

/* Hamburger-menu */
.hamburger-menu {
	display: inline-block;
	background-color: rgb(244, 244, 244);
	padding: 20px 25px;

	& > a {
		display: inline-block;
		font-size: 25px;
		color: $theme-color;
	}
}
.side-mobile-menu {
	display: none;
	@media #{$md} {
		display: block;
	}
	@media #{$xs} {
		display: block;
	}
	@media #{$sm} {
		display: block;
	}
}

.offset-sidebar {
	display: block;
	margin-top: 30px;
	@media #{$md} {
		display: none;
	}
	@media #{$xs} {
		display: none;
	}
	@media #{$sm} {
		display: none;
	}
}

// //bootstrap-menu

.navbar-nav {
	padding-left: 35px;
	& .nav-item {
		position: relative;
		z-index: 1;
		&::before {
			content: "";
			position: absolute;
			background-color: #ededed;
			width: 2px;
			height: 10px;
			right: 31%;
			top: 50%;
			transform: translateY(-50%);
			-webkit-transform: translateY(-50%);
			-moz-transform: translateY(-50%);
			-ms-transform: translateY(-50%);
			-o-transform: translateY(-50%);
			z-index: 1;
			@include transition(0.3s);
			@media #{$lg} {
				right: -25%;
			}
		}

		&:last-child {
			&::before {
				display: none;
			}
			& .nav-link {
				margin-right: 0;
				&::before {
					display: none;
				}
			}
		}
		&.active {
			color: $heding;
			& .nav-link {
				color: $heding;
			}
		}
		& .nav-link {
			color: $heding;
			font-size: 14px;
			font-weight: 700;
			line-height: 1;
			display: inline-block;
			position: relative;
			margin: 0 72px 0 0;
			padding: 26px 0;
			@media #{$lg} {
				margin: 0 30px 0 0;
			}
			&::after {
				display: none;
			}

			&:hover {
				color: $theme-color;
			}
			& i {
				font-size: 10px;
			}
		}
		&:hover {
			& > .nav-link {
				color: $theme-color;
			}
		}
	}
}

.navbar {
	& .dropdown-menu {
		border: 0;
		position: absolute;
		z-index: 5;
		background-color: #fff;
		border-radius: 0;
		display: block;
		right: 0;
		left: -35px;
		padding: 8px 0;
		top: 100%;
		visibility: hidden;
		transform: scaleY(0);
		opacity: 0;
		min-width: 220px;
		box-shadow: 0px 30px 70px 0px rgba(0, 0, 0, 0.05);
		margin: 0;
		transform-origin: 0 0;
		-webkit-transition: all 0.3s ease-in-out;
		-moz-transition: all 0.3s ease-in-out;
		-ms-transition: all 0.3s ease-in-out;
		-o-transition: all 0.3s ease-in-out;
		transition: all 0.3s ease-in-out;
		& .dropdown-item {
			color: #505050;
			font-size: 14px;
			font-weight: 600;
			padding: 9px 35px;
			display: block;
			transition: all 0.3s ease-in-out;
			&:hover {
				transform: translatex(5px);
				color: $theme-color;
				background-color: transparent;
				-webkit-transform: translatex(5px);
				-moz-transform: translatex(5px);
				-ms-transform: translatex(5px);
				-o-transform: translatex(5px);
			}
		}
	}

	& .dropdown:hover {
		& .dropdown-menu {
			opacity: 1;
			visibility: visible;
			transform: scale(1);
		}
	}
	.navbar-nav {
		.nav-item {
			&.active {
				& > .nav-link {
					color: $theme-color;
					position: relative;
				}
			}
		}
	}
}

//sticky-menu
.sticky-menu {
	position: fixed;
	width: 100%;
	background: $white;
	top: 0;
	animation: 300ms ease-in-out 0s normal none 1 running fadeInDown;
	@include transition(0.4s);
	box-shadow: 0 0 10px 3px rgba(0, 0, 0, 0.05);
	padding-top: 0;
	padding-bottom: 0;
	& .logo {
		transform: translateY(2px);
	}

	& .hamburger-menu {
		top: 17px;

		@media #{$md} {
			top: 35px;
		}
	}
	.navbar-nav {
		& .nav-item {
			& .nav-link {
				color: #505050;
			}
		}
	}
}
.left-cta {
	& span {
		color: $white;
		font-size: 14px;
		line-height: 26px;
		display: inline-block;
		padding-top: 6px;
		padding-bottom: 6px;
		@media #{$xs} {
			font-size: 13px;
		}
		&:first-child {
			margin-right: 30px;
			@media #{$xs} {
				display: none;
			}
		}
		& i {
			color: #61869a;
			font-size: 14px;
			font-weight: 300;
			line-height: 26px;
			transform: translateY(1px);
			-webkit-transform: translateY(1px);
			-moz-transform: translateY(1px);
			-ms-transform: translateY(1px);
			-o-transform: translateY(1px);
			margin-right: 3px;
		}
	}
}
.right-language {
	.language-btn {
		color: $white;
		font-size: 14px;
		font-weight: 400;
		& i {
			font-size: 14px;
		}
		&::after {
			display: none;
		}
	}
	& .dropdown-menu {
		min-width: 170px;
		padding: 10px 0;
		background: $white;
		box-shadow: 0px 25px 50px 0px rgba(213, 216, 223, 0.5);
		border: 1px solid #f8f8f8;
		border-radius: 0;
		top: 20px;
		text-align: left;
		padding-left: 8px;
		@media #{$xs} and (orientation:landscape) {
			top: 165px !important;
		}
		& ul {
			& li {
				& a {
					display: block;
					padding: 0 15px;
					font-size: 15px;
					color: #767a89;
					line-height: 35px;
					text-transform: uppercase;
				}
			}
		}
	}
}
.bottom-border-line {
	border-bottom: 1px solid rgb(26, 86, 118);
}
.right-query {
	display: flex;
	align-items: center;
	justify-content: end;
	li {
		&:first-child,
		&:nth-child(2) {
			display: flex;
			align-items: center;
		}
		&:first-child {
			padding-right: 40px;
		}
		&:nth-child(2) {
			padding-right: 60px;
			@media #{$lg} {
				padding-right: 40px;
			}
			@media #{$md} {
				padding-right: 40px;
			}
		}

		.icon {
			background-color: rgb(17, 71, 100);
			width: 26px;
			height: 26px;
			text-align: center;
			line-height: 26px;
			color: $white;
			font-size: 14px;
			font-weight: 300;
			margin-right: 14px;
		}
		span {
			color: #a0afb7;
			font-size: 16px;
			font-weight: 400;
			line-height: 26px;
			display: block;
			strong {
				color: $white;
				font-weight: 500;
				border-bottom: 1px solid $white;
				&:hover {
					color: $theme-color;
					border-color: $theme-color;
				}
			}
		}
	}
}

//transparent-header
.transparent-header {
	position: absolute;
	left: 0;
	right: 0;
	z-index: 111;
	top: 0;
	.header-top {
		padding-top: 2px;
		padding-bottom: 3px;
	}
	.bottom-border-line {
		border-color: rgba(255, 255, 255, 0.2);
	}
	.left-cta {
		& span {
			& i {
				color: $white;
				margin-right: 4px;
			}
		}
	}
}
.ham-menu-2 {
	background-color: transparent;
	a {
		font-size: 18px;
	}
}

//menu-style-2
.menu-style-2 {
	top: 7px;
	&.sticky-menu {
		top: 0;
	}
	.sticky-logo {
		display: none;
		@include transition(0.3s);
	}
	.main-logo {
		display: inline-block;
		@include transition(0.3s);
	}
	.navbar-nav {
		.nav-item {
			&::before {
				display: none;
			}
			&:last-child {
				.nav-link {
					margin-right: 0;
				}
			}
			.nav-link {
				color: $white;
				margin: 0 50px 0 0;
				padding: 30px 0;
				@media #{$lg} {
					margin-right: 35px;
				}
				&::before {
					display: none;
				}
				&:hover {
					color: $theme-color;
				}
			}
		}
	}

	&.sticky-menu {
		.sticky-logo {
			display: inline-block;
		}
		.main-logo {
			display: none;
		}
		.navbar-nav {
			.nav-item {
				.nav-link {
					color: $heding;
				}
			}
		}
	}
	.navbar .dropdown-menu .dropdown-item {
		&:hover {
			color: $theme-color;
		}
	}
}

//header-top-3
.header-top-3 {
	padding-top: 2px;
	padding-bottom: 3px;
}
.left-cta-3 {
	span {
		& i {
			color: $white;
			margin-right: 5px;
		}
	}
}

.menu-style-3 {
	top: 0;
	.navbar-nav {
		.nav-item {
			&::before {
				display: none;
			}
			.nav-link {
				color: $heding3;
				margin: 0 46px 0 0;
				padding: 30px 0;
				&::before {
					display: none;
				}
				& i {
					color: $theme-color2;
				}
				&:hover {
					color: $theme-color2;
				}
			}
			&:hover {
				& > .nav-link {
					color: $theme-color2;
				}
			}
		}
		.nav-item {
			&.active {
				& > .nav-link {
					color: $theme-color2;
					position: relative;
					i {
						color: $theme-color2;
					}
				}
			}
		}
	}

	.right-nav {
		padding: 0;
		margin: 0;
		.quote-btn {
			background: $theme-color2;
			margin-top: 5px;
			&:hover {
				background: $heding3;
			}
		}
	}
	.hamburger-menu {
		background-color: transparent;
	}
	.navbar .dropdown-menu .dropdown-item {
		&:hover {
			color: $theme-color2;
		}
	}
}
