//slider-area
.custom-container-slider {
    max-width: 1430px;
    overflow: hidden;
}
.main-slider {
    position: relative;
    top: -190px;
}
.slider-bg {
    margin-top: -16px;
    position: relative;
    background: url(../img/slider/hero-bg-1.jpg) no-repeat center;
    background-size: cover;
    min-height: 796px;
    display: flex;
    align-items: center;
    z-index: 2;
    @media #{$lg} {
        min-height: 620px;
    }
    @media #{$md} {
        min-height: 550px;
    }
    @media #{$xs} {
        min-height: 500px;
    }

    &::before {
        position: absolute;
        content: "";
        background-color: rgb(8, 67, 98);
        opacity: 0.98;
        width: 100%;
        height: 100%;
        left: 0;
        top: 0;
        z-index: -1;
    }
    .slider__content {
        padding-left: 50px;
        margin-top: 42px;
        @media #{$laptop} {
            padding-left: 0;
        }
        @media #{$lg} {
            padding-left: 0;
        }
        @media #{$md} {
            padding-left: 0;
            padding-top: 50px;
            padding-bottom: 60px;
        }
        @media #{$xs} {
            padding-left: 0;
            padding-top: 50px;
            padding-bottom: 60px;
        }
    }
    .slider-right {
        padding-top: 63px;
        margin-left: -32px;
        @media #{$lg} {
            padding-top: 0;
            margin-left: 0;
        }
        @media #{$md} {
            margin-left: 0;
        }
        @media #{$xs} {
            margin-left: 0;
        }
    }
}

#slider-one .owl-item.active h1 {
    -webkit-animation-duration: 600ms;
    animation-duration: 600ms;
    -webkit-animation-fill-mode: both;
    animation-fill-mode: both;
    animation-name: fadeInLeft;
    animation-delay: 0.3s;
}

#slider-one .owl-item.active p {
    -webkit-animation-duration: 800ms;
    animation-duration: 1s;
    -webkit-animation-fill-mode: both;
    animation-fill-mode: both;
    animation-name: fadeInLeft;
    animation-delay: 0.4s;
}

#slider-one .owl-item.active .btn-list {
    -webkit-animation-duration: 1000ms;
    animation-duration: 1s;
    -webkit-animation-fill-mode: both;
    animation-fill-mode: both;
    animation-name: fadeInLeft;
    animation-delay: 0.3s;
}
#slider-one .owl-item.active .slider-right {
    -webkit-animation-duration: 2s;
    animation-duration: 1s;
    -webkit-animation-fill-mode: both;
    animation-fill-mode: both;
    animation-name: fadeInRight2;
    animation-delay: 0.4s;
    @media #{$md} {
        animation-name: fadeIn;
    }
    @media #{$xs} {
        animation-name: fadeIn;
    }
}

.main-slider-area {
    .dodlle-one {
        position: absolute;
        border-radius: 50%;
        background-color: rgb(255, 168, 75);
        width: 7px;
        height: 7px;
        z-index: 12;
        right: 46.09%;
        top: 260px;
        &.two {
            top: 468px;
            right: 27.03%;
        }
        &.three {
            top: 77%;
            left: 19.2%;
        }
    }
    .owl-nav {
        button {
            border: 2px solid rgba(255, 255, 255, 0.06) !important;
            width: 64px;
            height: 64px;
            line-height: 64px !important;
            display: inline-block;
            border-radius: 6px;
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            -webkit-transform: translateY(-50%);
            -moz-transform: translateY(-50%);
            -ms-transform: translateY(-50%);
            -o-transform: translateY(-50%);
            z-index: 5;
            color: rgba(255, 255, 255, 0.06) !important;
            & i {
                font-size: 18px;
            }
            &.owl-prev {
                left: 60px;
            }
            &.owl-next {
                right: 60px;
            }
            &.owl-prev:hover,
            &.owl-next:hover {
                color: $white !important;
                border-color: $theme-color;
                background: $theme-color;
            }
        }
    }
}
.owl-dots {
    position: absolute;
    z-index: 11;
    left: 40px;
    bottom: 40px;
    button.owl-dot {
        border: 1px solid $body-text-color;
        width: 15px;
        height: 15px;
        border-radius: 50%;
        margin-right: 15px;
        &.active {
            border-color: $theme-color;
            background-color: $theme-color;
        }
    }
}

.slider__content {
    .main-title-one {
        margin-bottom: 10px;
        @media #{$laptop} {
            font-size: 70px;
        }
    }
    p {
        color: $grey;
        font-size: 18px;
        line-height: 28px;

        & strong {
            color: $white;
            font-weight: 700;
        }
    }
}

.sub-title {
    color: $heding;
    font-size: 16px;
    font-weight: 500;
    font-family: $gt-font;
    text-transform: uppercase;
    line-height: 23px;
}

.main-title-one {
    color: $heding;
    font-size: 80px;
    color: $white;
    line-height: 1.15;
    letter-spacing: -0.3px;
    @media #{$lg} {
        font-size: 56px;
    }
    @media #{$md} {
        font-size: 56px;
    }
    @media #{$xs} {
        font-size: 50px;
    }
    & span {
        font-weight: 100;
        letter-spacing: -0.6px;
    }
}

.bottom-shape {
    display: inline-block;
    position: relative;
    z-index: 2;
    &::before {
        position: absolute;
        content: "";
        background: url(../img/shape/line-1.svg) no-repeat;
        width: 100%;
        height: 14px;
        left: 7px;
        top: 65%;
        z-index: -1;
    }
}

//slider-wrapper

.slider-bg-2 {
    position: relative;
    background-size: cover;
    background-position: center;
    width: 100%;
    background-repeat: no-repeat;
    min-height: 970px;
    z-index: 1;
    display: flex;
    align-items: center;
    @include transition(0.3s);
    @media #{$lg} {
        min-height: 750px;
    }
    @media #{$md} {
        min-height: 550px;
    }
    @media #{$xs} {
        min-height: 500px;
    }

    &::before {
        position: absolute;
        content: "";
        background-color: rgba(0, 54, 96, 0.4);
        opacity: 0.98;
        width: 100%;
        height: 100%;
        left: 0;
        top: 0;
        z-index: -1;
    }

    img.shape-one {
        width: 311px;
        height: 311px;
        display: inline-block;
        position: absolute;
        top: 315px;
        right: 17%;
        @media #{$lg} {
            display: none;
        }
        @media #{$md} {
            display: none;
        }
        @media #{$xs} {
            display: none;
        }
    }

    .slider__content {
        padding-top: 155px;
        padding-left: 8px;
        .main-title-two {
            margin-bottom: 53px;
        }
    }
}
.shape-h2-one {
    width: 336px;
    height: 350px;
    display: inline-block;
    position: absolute;
    left: 0;
    bottom: 0;
    z-index: 11;
    @media #{$md} {
        display: none;
    }
    @media #{$xs} {
        display: none;
    }
}

.slide-tag-list {
    padding: 0;
    margin-bottom: 12px;
    li {
        padding-right: 51px;
        display: inline-block;
        &:last-child {
            a {
                &::before {
                    display: none;
                }
            }
        }
        @media #{$xs} {
            padding-right: 10px;
            & a {
                font-size: 14px;
            }
        }
        a {
            color: rgba(255, 255, 255, 0.502);
            font-size: 16px;
            font-weight: 700;
            display: inline-block;
            position: relative;
            z-index: 2;
            &::before {
                content: "";
                position: absolute;
                background-color: rgba(255, 255, 255, 0.302);
                width: 20px;
                height: 1px;
                right: -38px;
                top: 12px;
                z-index: 11;
                @media #{$xs} {
                    display: none;
                }
            }
        }
    }
}

.main-title-two {
    color: $white;
    font-size: 120px;
    line-height: 1.02;
    @media #{$lg} {
        font-size: 80px;
    }
    @media #{$md} {
        font-size: 56px;
    }
    @media #{$xs} {
        font-size: 50px;
    }
}

#slider-one .slider-bg-2 .owl-item.active .theme_btn {
    -webkit-animation-duration: 800ms;
    animation-duration: 800ms;
    -webkit-animation-fill-mode: both;
    animation-fill-mode: both;
    animation-name: fadeInUp;
    animation-delay: 0.4s;
}
//slider-bg-3
.slider-bg-3 {
    min-height: 830px;
    display: flex;
    align-items: center;
    position: relative;
    z-index: 2;
    @media #{$lg} {
        min-height: 750px;
    }
    @media #{$md} {
        min-height: 550px;
    }
    @media #{$xs} {
        min-height: 500px;
    }

    &::before {
        content: "";
        position: absolute;
        background-color: rgb(4, 4, 4);
        opacity: 0.902;
        width: 100%;
        height: 100%;
        left: 0;
        top: 0;
        z-index: -1;
    }
    &::after {
        content: "";
        position: absolute;
        background: url(../img/slider/pattern-1.png) no-repeat;
        width: 100%;
        height: 100%;
        left: 0;
        top: 0;
        z-index: -1;
    }
}
.slider__content3 {
    position: relative;
    z-index: 5;
    .popup-video {
        color: $white;
        font-size: 14px;
        width: 80px;
        height: 80px;
        line-height: 80px;
        background: $theme-color2;
        margin-bottom: 24px;
    }
    .bottom-shape {
        &::before {
            background: url(../img/shape/line-3.png) no-repeat;
        }
    }
    .main-title-one {
        margin-bottom: 12px;
    }
    .paragraph-title {
        color: #a7bbc5;
        & strong {
            color: $white;
        }
    }
    .subscribe-form {
        position: relative;
        z-index: 1;
        input {
            background-color: $white;
            position: relative;
            height: 60px;
            color: #999999;
            &::placeholder {
                color: #999999;
            }
            &::-moz-placeholder {
                color: #999999;
            }
            &::-webkit-placeholder {
                color: #999999;
            }
            &::-o-placeholder {
                color: #999999;
            }
            @media #{$xs} {
                padding-left: 10px;
            }
        }
        .subscribe-btn {
            color: $white;
            font-size: 14px;
            font-weight: 700;
            line-height: 1;
            border: 0;
            background: $theme-color2;
            position: absolute;
            z-index: 2;
            max-width: 200px;
            width: 100%;
            // padding: 18px 0;
            height: 60px;
            right: 0;
            top: 0;
            @media #{$xs} {
                max-width: 115px;
            }
        }
    }
}

//main-slider-area
.nav-none {
    .owl-nav {
        display: none;
    }
}

.main-slider-area {
    position: relative;
    z-index: 2;
    .right-img {
        position: absolute;
        z-index: 3;
        left: 48px;
        bottom: -35px;
        @media #{$laptop} {
            display: none;
        }
        @media #{$lg} {
            display: none;
        }
        @media #{$md} {
            display: none;
        }
        @media #{$xs} {
            display: none;
        }
        &.two {
            top: 90px;
            right: 0;
            left: auto;
        }
    }
}

#slider-three .owl-item.active h1 {
    -webkit-animation-duration: 1.3s;
    animation-duration: 1.2s;
    -webkit-animation-fill-mode: both;
    animation-fill-mode: both;
    animation-name: fadeInDown;
    animation-delay: 0.6s;
}
#slider-three .owl-item.active p {
    -webkit-animation-duration: 1s;
    animation-duration: 1s;
    -webkit-animation-fill-mode: both;
    animation-fill-mode: both;
    animation-name: fadeInDown;
    animation-delay: 0.4s;
}
#slider-three .owl-item.active p {
    -webkit-animation-duration: 700ms;
    animation-duration: 700ms;
    -webkit-animation-fill-mode: both;
    animation-fill-mode: both;
    animation-name: fadeInDown;
    animation-delay: 0.2s;
}
#slider-three .owl-item.active .subscribe-form {
    -webkit-animation-duration: 1s;
    animation-duration: 1s;
    -webkit-animation-fill-mode: both;
    animation-fill-mode: both;
    animation-name: fadeInUp;
    animation-delay: 0.3s;
}
