//blog-area
.blogs {
    padding-bottom: 35px;
    position: relative;
    z-index: 1;
    .blogs__thumb {
        img {
            width: 100%;
        }
    }
    &::before {
        content: "";
        position: absolute;
        background: rgb(231, 231, 231);
        width: 100%;
        height: 1px;
        left: 0;
        bottom: 0;
        z-index: 1;
        @include transition(0.3s);
    }
    &::after {
        content: "";
        position: absolute;
        background: $theme-color;
        width: 0;
        height: 1px;
        left: 0;
        bottom: 0;
        z-index: 1;
        @include transition(0.3s);
    }
    &__thumb {
        overflow: hidden;
        position: relative;
        z-index: 1;
        & img {
            display: block;
            @include transition(0.3s);
        }
    }
    .tag {
        color: $white;
        font-size: 14px;
        font-weight: 700;
        line-height: 1;
        background: $theme-color;
        padding: 11px 21px;
        display: inline-block;
        position: absolute;
        right: 30px;
        top: 60%;
        z-index: 2;
        transform: translateY(-60%);
        @media #{$lg} {
            top: 42%;
        }
        @media #{$md} {
            top: 48%;
        }
        @media #{$xs} {
            top: 48%;
        }
    }
    &__meta {
        span {
            color: $body-text-color;
            font-size: 14px;
            &.author {
                color: $heding;
                font-weight: 700;
            }
        }
    }
    .blog-title {
        &:hover {
            text-decoration: underline;
        }
    }
    &:hover {
        &::before {
            width: 0;
            right: 0;
        }
        &::after {
            width: 100%;
        }
        .blogs__thumb {
            & img {
                transform: scale(1.1);
                -webkit-transform: scale(1.1);
                -moz-transform: scale(1.1);
                -ms-transform: scale(1.1);
                -o-transform: scale(1.1);
            }
        }
    }
}

.blog-title {
    color: $heding;
    font-size: 20px;
    line-height: 1.6;
    @include transition(0.3s);
}

//blog-2

.blog-2 {
    background: $white;
    padding-bottom: 30px;
    &::before {
        left: auto;
        right: -35px;
        top: 0;
        background-color: rgb(220, 220, 220);
        width: 1px;
        height: 100%;
        @media #{$xs} {
            display: none;
        }
    }
    &::after {
        display: none;
    }

    .blog-title2 {
        font-size: 30px;
        line-height: 1.3;
        display: inline-block;
        margin-bottom: 13px;
        @media #{$md} {
            font-size: 20px;
        }
        @media #{$xs} {
            font-size: 20px;
        }
        &:hover {
            text-decoration: underline;
        }
    }
    .blogs__meta {
        margin-bottom: 9px;
        &.bm-2 {
            margin-bottom: 6px;
        }
    }
    .blog-content {
        padding-left: 40px;
        padding-right: 30px;
        padding-bottom: 5px;
        @media #{$md} {
            padding-left: 15px;
            padding-right: 15px;
        }
        @media #{$xs} {
            padding-left: 15px;
            padding-right: 15px;
        }
        .tag {
            top: 56%;
            @media #{$lg} {
                top: 56%;
            }
            @media #{$md} {
                top: 53%;
            }
            @media #{$xs} {
                top: 43%;
            }
        }
    }
    &:hover {
        &::before {
            display: none;
        }
    }
}
.blog-text {
    font-size: 20px;
    a {
        color: $theme-color;
        font-size: 14px;
    }
    &:hover {
        a {
            color: $heding;
        }
    }
}
.blog-2b {
    &::before {
        display: none;
    }
}
//blog-area3
.blog-area3 {
    .semi-title2 {
        &::after {
            display: none;
        }
    }
    &.portfolio-nav {
        .owl-nav {
            text-align: end;
            position: absolute;
            top: -135px;
            right: 0;
            left: auto;
            button {
                font-size: 18px;
                border: 2px solid rgb(242, 242, 242);
                background: $white;
                width: 54px;
                height: 54px;
                line-height: 54px;
                color: $heding3;
                &.owl-next {
                    color: $heding3;
                    margin-left: 15px;
                    &:hover {
                        color: $white;
                        background: $theme-color2;
                    }
                }
                &.owl-prev {
                    color: $heding3;
                    &:hover {
                        color: $white;
                        background: $theme-color2;
                    }
                }
            }
        }
    }
}
.blogs3 {
    padding-bottom: 0;
    &::before {
        border: 1px solid rgb(231, 231, 231);
        background: $white;
        box-shadow: 0px 16px 32px 0px rgba(0, 0, 0, 0.04);
        left: 0;
        top: 0;
        width: 92%;
        height: 100%;
        z-index: -1;
    }
    .tag {
        color: $theme-color2;
        background: $white;
        left: 30px;
        top: 41px;
        right: auto;
        padding: 4px 8px;
    }
    .blogs-content {
        padding: 23px 70px 23px 30px;
        @media #{$lg} {
            padding: 23px 30px 30px 20px;
        }
        @media #{$xs} {
            padding: 23px 15px 30px 15px;
        }
        .blogs__meta {
            margin-bottom: 6px;
        }
        .blog-title {
            line-height: 1.5;
        }
    }
    .read-btn {
        color: #cecdcd;
        display: inline-block;
        padding: 0;
        transform: translateY(0);
        margin-top: 20px;
        margin-bottom: 0;
        font-size: 16px;
        & i {
            font-size: 14px;
            font-weight: 700;
            margin-left: 0;
            transform: scale(0);
            -webkit-transform: scale(0);
            -moz-transform: scale(0);
            -ms-transform: scale(0);
            -o-transform: scale(0);
            display: none;
            @include transition(0.3s);
        }
        &:hover {
            color: $theme-color2;
            & i {
                display: inline-block;
                transform: scale(1);
                -webkit-transform: scale(1);
                -moz-transform: scale(1);
                -ms-transform: scale(1);
                -o-transform: scale(1);
            }
        }
    }
}

//blog-4
.blog-4 {
    border: 1px solid rgb(231, 231, 231);
    &::before {
        display: none;
    }
    &::after {
        display: none;
    }
    .blog__thumb {
        position: relative;
    }
    .blog-content {
        padding: 0 55px 7px 50px;
        @media #{$lg} {
            padding: 0 20px 0 20px;
        }
        @media #{$xs} {
            padding: 0 15px 0 15px;
        }
    }
    .blog-title {
        font-size: 34px;
        line-height: 1.4;
        display: inline-block;
        margin-bottom: 12px;
        @media #{$lg} {
            font-size: 24px;
        }
        @media #{$md} {
            font-size: 24px;
        }
        @media #{$xs} {
            font-size: 20px;
        }
    }
    p {
        margin-bottom: 25px;
    }

    .blogs__meta {
        border-top: 1px solid rgb(231, 231, 231);
        padding-top: 19px;
        span {
            display: inline-block;
            margin-right: 65px;
            position: relative;
            @media #{$xs} {
                margin-right: 8px;
            }
            &.border-line2 {
                &::before {
                    content: "";
                    position: absolute;
                    background: #e7e7e7;
                    left: -30px;
                    top: 7px;
                    width: 2px;
                    height: 10px;
                    z-index: 1;
                    @media #{$xs} {
                        display: none;
                    }
                }
                &::after {
                    content: "";
                    position: absolute;
                    background: #e7e7e7;
                    right: -30px;
                    top: 7px;
                    width: 2px;
                    height: 10px;
                    z-index: 1;
                    @media #{$xs} {
                        display: none;
                    }
                }
            }
            i {
                color: $heding;
                margin-right: 4px;
            }
        }
    }
    .tag {
        right: auto;
        left: 50px;
        top: auto;
        bottom: -37px;
        display: inline-block;
        @media #{$xs} {
            display: none;
        }
    }
    .commentor {
        background-color: $white;
        box-shadow: 0px 16px 32px 0px rgba(0, 0, 0, 0.04);
        position: absolute;
        right: 50px;
        bottom: -30px;
        display: inline-block;
        z-index: 1;
        padding: 10px 35px 10px 10px;
        img {
            width: 40px;
            height: 40px;
            display: inline-block;
        }
        b {
            color: $heding;
        }
    }
    .popup-video {
        color: $heding;
        width: 80px;
        height: 80px;
        position: absolute;
        left: 50%;
        top: 50%;
        line-height: 80px;
        font-size: 14px;
        transform: translate(-50%, -50%);
        -webkit-transform: translate(-50%, -50%);
        -moz-transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%);
        -o-transform: translate(-50%, -50%);
        @media #{$xs} {
            width: 50px;
            height: 50px;
            line-height: 50px;
        }
        &:hover {
            color: $white;
        }
    }
    .owl-nav {
        position: absolute;
        top: 45%;
        transform: translateY(-45%);
        left: 0;
        right: 0;
        z-index: 3;
        -webkit-transform: translateY(-45%);
        -moz-transform: translateY(-45%);
        -ms-transform: translateY(-45%);
        -o-transform: translateY(-45%);
        button {
            color: rgba(250, 250, 250, 0.4);
            position: absolute;
            z-index: 1;
            i {
                font-size: 30px;
            }
            &:hover {
                color: $white;
            }
            &.owl-next {
                left: auto;
                right: 30px;
            }
            &.owl-prev {
                left: 30px;
            }
        }
    }
}

.quote-box {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 64px 35px 58px 50px;
    position: relative;
    z-index: 1;
    @media #{$lg} {
        padding: 45px 15px 45px 15px;
    }
    @media #{$md} {
        padding: 45px 15px 45px 15px;
    }
    @media #{$xs} {
        display: block;
        padding: 45px 15px 45px 15px;
    }
    &::before {
        content: "";
        position: absolute;
        background: url(../img/blog/icon-lg.png) no-repeat;
        right: 30px;
        top: 36px;
        width: 146px;
        height: 151px;
        z-index: -1;
    }
    .quote-icon {
        width: 78px;
        height: 80px;
        margin-right: 30px;
        @media #{$xs} {
            margin-bottom: 15px;
        }
    }
    .blog-title {
        font-size: 34px;
        line-height: 1.4;
        @media #{$lg} {
            font-size: 24px;
        }
        @media #{$md} {
            font-size: 24px;
        }
        @media #{$xs} {
            font-size: 20px;
        }
    }
}

.page-navigation {
    .page-item:first-child .page-link {
        border-top-left-radius: 0;
        border-bottom-left-radius: 0;
    }
    .page-item:last-child .page-link {
        border-top-right-radius: 0;
        border-bottom-right-radius: 0;
    }
    .page-item:last-child .page-link:hover {
        img {
            filter: brightness(1) invert(1);
        }
    }
    .page-link {
        color: $body-text-color;
        font-size: 14px;
        font-weight: 700;
        border: 1px solid rgb(238, 238, 238);
        background: rgba(246, 246, 246, 0);
        width: 54px;
        height: 50px;
        line-height: 50px;
        text-align: center;
        margin: 0 7px;
        padding: 0;
        @media #{$xs} {
            font-size: 14px;
            width: 40px;
            height: 40px;
            line-height: 42px;
            margin: 0 2px;
        }
        &:hover,
        &:focus {
            z-index: 2;
            color: $white;
            background: $heding;
            border-color: $heding;
            outline: none;
            box-shadow: none;
        }
    }
}

//blog-post

.blog-post {
    display: flex;
    align-items: center;
    .post-content {
        margin-left: 20px;
        h6 {
            line-height: 1.4;
            a {
                color: $heding;
                &:hover {
                    color: $theme-color;
                }
            }
        }
        span {
            i {
                color: $heding;
                margin-right: 5px;
            }
        }
    }
}

//news-details-left
.news-details-left {
    border: 2px solid rgb(244, 244, 244);
    padding: 45px 40px;
    @media #{$lg} {
        padding: 50px 15px;
    }
    @media #{$md} {
        padding: 50px 0;
    }
    @media #{$xs} {
        padding: 50px 10px;
    }
    .blogs__meta {
        margin-bottom: 20px;
        .border-line2 {
            position: relative;
            z-index: 1;
            margin-left: 65px;
            margin-right: 65px;
            @media #{$xs} {
                margin-left: 8px;
                margin-right: 8px;
            }
            &::before {
                content: "";
                position: absolute;
                background: #e7e7e7;
                left: -35px;
                top: 4px;
                width: 2px;
                height: 10px;
                z-index: 1;
                @media #{$xs} {
                    display: none;
                }
            }
            &::after {
                content: "";
                position: absolute;
                background: #e7e7e7;
                right: -35px;
                top: 4px;
                width: 2px;
                height: 10px;
                z-index: 1;
                @media #{$xs} {
                    display: none;
                }
            }
        }
    }
}
.boxes {
    overflow: hidden;
    .box-img {
        float: left;
        margin-right: 30px;
        margin-top: 12px;
        @media #{$xs} {
            float: none;
            margin-bottom: 20px;
            width: 100%;
        }
    }
}

.blog-block {
    padding: 50px 50px 42px 50px;
    border: 2px solid rgb(231, 231, 231);
    @media #{$xs} {
        padding: 30px 10px;
    }
    .icon {
        color: $white;
        font-size: 20px;
        line-height: 60px;
        width: 60px;
        height: 60px;
        border-radius: 50%;
        display: inline-block;
        text-align: center;
        background: $theme-color;
        margin-bottom: 25px;
    }
    h3 {
        line-height: 1.3;
        @media #{$md} {
            font-size: 24px;
        }
        @media #{$xs} {
            font-size: 20px;
        }
    }
}

.share-social {
    border-bottom: 1px solid rgb(231, 231, 231);
    .author__social {
        a {
            color: #b9b9b9;
            font-size: 18px;
            margin-left: 27px;
            &:hover {
                color: $heding;
            }
        }
    }
}

.more-post {
    border-bottom: 1px solid rgb(231, 231, 231);
}

.prev-box {
    h3 {
        @media #{$lg} {
            font-size: 24px;
        }
    }
}
.authors {
    overflow: hidden;
    border: 1px solid rgb(231, 231, 231);
    padding: 40px 35px 20px 40px;
    @media #{$xs} {
        padding: 40px 10px 20px 10px;
    }
    .author-img {
        float: left;
        margin-right: 38px;
        margin-bottom: 20px;
        @media #{$xs} {
            float: none;
            width: 100%;
            margin-right: 0;
        }
    }
    .author-content {
        overflow: hidden;
        margin-top: 13px;
        h3 {
            font-size: 36px;
            margin-bottom: 12px;
            @media #{$laptop} {
                font-size: 32px;
            }
            @media #{$lg} {
                font-size: 24px;
            }
            @media #{$md} {
                font-size: 24px;
            }
            @media #{$xs} {
                font-size: 20px;
            }
        }
    }
}
.comment-list {
    padding-left: 0;
    @media #{$xs} {
        padding-left: 0;
    }
    li {
        padding-bottom: 20px;
        margin-bottom: 45px;
        border-bottom: 2px solid rgb(243, 243, 243);
        @media #{$xs} {
            margin-left: 0;
        }
    }
}
.comment-box {
    position: relative;
    z-index: 1;
    .comment-avatar {
        float: left;
        margin-right: 30px;
        margin-top: -15px;
        @media #{$xs} {
            float: none;
            margin-right: 0;
        }
        img {
            border-radius: 50%;
            -webkit-border-radius: 50%;
            -moz-border-radius: 50%;
            -ms-border-radius: 50%;
            -o-border-radius: 50%;
        }
    }
    .comment-content {
        overflow: hidden;
        margin-top: 15px;
        p {
            i {
                color: $heding;
            }
        }
    }
    .reply {
        font-size: 14px;
        border: 2px solid rgb(243, 243, 243);
        font-weight: 400;
        padding: 10px 22px;
        line-height: 1;
        position: absolute;
        z-index: 1;
        right: 0;
        top: 0;
        i {
            color: $heding;
        }
        &:hover {
            color: $white;
            background: $heding;
            i {
                color: $white;
            }
        }
    }
}

.comment-form {
    .contact-form {
        border: 2px solid rgb(244, 244, 244);
        padding: 50px 40px 35px 40px;
        @media #{$xs} {
            padding: 30px 10px 15px 10px;
        }
        .input-box {
            &::before {
                left: auto;
                right: 30px;
                font-size: 14px;
            }
            &.text-input::before {
                content: "\f303";
            }
            &.sub-input::before {
                content: "\f0ac";
            }
        }
        textarea {
            border: 2px solid rgb(244, 244, 244);
            box-shadow: none;
            padding-left: 30px;
        }
        input {
            border: 2px solid rgb(244, 244, 244);
            box-shadow: none;
            padding-left: 30px;
            height: 60px;
        }
    }
}
.post-title {
    font-size: 26px;
}
